*{
  padding:0;
  box-sizing: border-box;
  margin:0;
}

.content{
  height: 80.5vh;
}

.register{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}

.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color:black;
}

.inactive-icon{
  color: grey;
}

.switch-icons{
  border: 1px solid black;
  border-radius: 5px;
  padding: 5px 20px;
}

.homepage-content{
  height: 70vh;
}
.card-header{
  font-size: medium;
}

.navbar{
  background-color: #FFFDD0 !important;
  box-sizing: border-box;
  box-shadow: 0 0 3px grey;
}

.nav-brand{
  padding: 0 10px 0 10px;
}

.ant-card{
  background-color: rgba(0, 0, 0, 0.916);
  border: rgba(0, 0, 0, 0.936);
  color: white;
  padding: 50px 20px;
}
